import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { OwnersService } from './owners.service';
import { StoresService } from '../stores/stores.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

import {
  ExportAsService,
  ExportAsConfig,
  SupportedExtensions,
} from 'ngx-export-as';

@Component({
  selector: 'app-owners',
  templateUrl: './owners.component.html',
  styleUrls: ['./owners.component.scss'],
})
export class OwnersComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  constructor(
    private modalService: BsModalService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private ownerService: OwnersService,
    private storesService: StoresService,
    private exportAsService: ExportAsService
  ) {}

  ngOnInit(): void {
    this.spinner.show();

    this.ownerService.getAll();
    this.ownerService.owners.subscribe(async (data) => {
      if (data) {
        this.items = data;
        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });

    this.spinner.hide();
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter(
        (item) =>
          item.name.toLowerCase().includes(FilterRecord) ||
          item.ownerId?.store?.name.toLowerCase().includes(FilterRecord)
      );
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }
  // Start select dropdown
  selectStoresList: InputSelectItem[];
  selectedStoreId: string;
  getStoresList() {
    this.spinner.show();

    this.storesService.getAll();

    this.storesService.stores.subscribe((data) => {
      if (data) {
        this.selectStoresList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: ``,
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectStore(event) {
    if (event) {
      this.selectedStoreId = event._id;
    }
  }
  // End select dropdown

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  addOwner() {
    this.spinner.show();

    this.modalRef.hide();
    this.ownerService.addOwner(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ownerService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  deleteOwner() {
    this.spinner.show();

    this.modalRef.hide();
    this.ownerService.deleteOwner(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ownerService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  addOwnerToStore() {
    this.spinner.show();

    this.modalRef.hide();
    this.ownerService
      .addOwnerToStore(this.itemId, this.selectedStoreId)
      .subscribe(
        (res) => {
          console.log(res);
          this.toastr.info(res['message'], 'Info');
          this.items = [];

          this.ownerService.getAll();
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );

    this.spinner.hide();
  }

  lockElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.ownerService.lockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ownerService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  unlockElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.ownerService.unlockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ownerService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  exportAsConfig: ExportAsConfig = {
    type: 'pdf',
    elementIdOrContent: 'my-table',
    options: {
      jsPDF: {
        orientation: 'landscape',
      },
    },
  };
  exportAs(type: SupportedExtensions, opt?: string) {
    this.exportAsConfig.type = type;
    if (opt) {
      this.exportAsConfig.options.jsPDF.orientation = opt;
    }
    this.exportAsService.save(this.exportAsConfig, 'Owners').subscribe(() => {
      // save started
    });
    this.exportAsService.get(this.exportAsConfig).subscribe((content) => {
      console.log(content);
    });
  }
}
