import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class OwnersService {
  constructor(private http: HttpClient) {}

  owners = new BehaviorSubject(null);

  getAll() {
    return this.http.get(baseUrl + '/owner').subscribe((res) => {
      this.owners.next(res['data']);
    });
  }

  addOwner(ownerId) {
    return this.http.post(baseUrl + `/owner/addOwner/${ownerId}`, {});
  }

  deleteOwner(ownerId) {
    return this.http.delete(baseUrl + `/owner/deleteOwner/${ownerId}`);
  }

  addOwnerToStore(userId, storeId) {
    return this.http.post(
      baseUrl + `/owner/addOwnerToStore/${userId}/${storeId}`,
      {}
    );
  }

  lockOne(itemId) {
    return this.http.get(baseUrl + `/owner/lock/${itemId}`);
  }

  unlockOne(itemId) {
    return this.http.get(baseUrl + `/owner/unlock/${itemId}`);
  }
}
